:root {
  --color-bg-dark: rgba(64, 174, 180, .94);
  --animation-card: 250ms;
  --color-body: white;
}

* {
  box-sizing: border-box;
}

body, p, ul {
  margin: 0;
}

body {
  -moz-osx-font-smoothing: grayscale;
  color: var(--color-black);
  font-size: 16px;
}

.search-form {
  background-color: var(--color-bg-dark);
  font: inherit;
  z-index: 1;
  width: 100%;
  margin-bottom: 16px;
  padding-top: 8px;
  padding-bottom: 8px;
  position: fixed;
}

.search-form__wrapper {
  max-width: 320px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  position: relative;
}

.search-form__input {
  height: 40px;
  width: 100%;
  border: none;
  outline: none;
  padding: 0 15px;
  font-family: inherit;
  font-size: 16px;
}

.search-form__button {
  color: var(--color-black);
  cursor: pointer;
  height: auto;
  width: 50px;
  transition: background-color var(--animation-duration) var(--timing-function);
  background-color: #f1eff1;
  border: none;
  font-size: 16px;
}

.search-form__button--delete {
  width: 30px;
  height: 30px;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
}

.search-form__button:hover {
  background-color: #d5cfd5;
}

.square {
  width: 50px;
  background-color: #fff;
  justify-content: center;
  align-items: center;
  display: flex;
}

.gallery {
  width: 95%;
  max-width: 1460px;
  grid-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: minmax(150px, 350px);
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 80px 15px 15px;
  list-style: none;
  display: grid;
}

.gallery__item {
  transition: transform var(--animation-duration) var(--timing-function);
  border-radius: 4px;
  position: relative;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__item:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  max-height: 270px;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  color: inherit;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.info {
  height: 80px;
  justify-content: space-between;
  padding: 15px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.photo-card {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.load-more {
  background-color: var(--color-bg-dark);
  color: #fff;
  cursor: pointer;
  transition: filter var(--animation-duration) var(--timing-function);
  border: none;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  padding: 12px 24px;
  font-family: inherit;
  display: flex;
}

.load-more:hover {
  filter: brightness(1.3);
}

.hidden, #loading.hidden {
  display: none;
}

#loading {
  z-index: 9999;
  width: 100%;
  height: 100%;
  background: url("color.0bb81b51.gif") center no-repeat;
  display: block;
  position: absolute;
}

